<template lang="html">
  <div class="form-select">
    <div class="form-select-item" :class="{'borderB': borderB}">
      <div class="form-input__label" :style="{'width': width}">{{label}}</div>
      <div class="form-input__select">
        <select class="js-select1" @change="$emit('change', curValue)" v-model="curValue">
          <option value="" style="display:none">请选择开户行</option>
          <option v-for="item in bankList" :value="item.id || item.name" >{{item.name}}</option>
        </select>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    bankList: Array,
    width: {
      type: String,
      default: '1.8rem'
    },
    color: {
      type: String,
      default: '#333'
    },
    paddingLeft: {
      type: String,
      default: '0rem'
    },
    label: {
      type: String,
      required: true
    },
    placeholder: {
      type: String,
      default: '请输入'
    },
    value: {},
    disabled: {
      type: Boolean,
      default: false
    },
    borderB: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    value (val) {
      this.curValue = val
    },
    curValue (val) {
      this.$emit('input', val)
    }
  },
  data () {
    return {
      curValue: this.value
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';

.form-select {
  overflow: hidden;
  font-size: .32rem;
  color: @midleColor;
  padding: 0 .22rem;
  background-color: @whiteColor;
  line-height: .88rem;
  .form-select-item {
    height: .88rem;
  }
  .form-input__label {
    float: left;
    text-align: left;
  }
  .form-input__select {
    float: left;
    width: 76%;
    .js-select1 {
      width: 100%;
      height: .88rem;
      font-size: .32rem;
      border: none;
      outline: none;
      color: @defaultColor;
      /*将默认的select选择框样式清除*/
      appearance:none;
      -moz-appearance:none;
      -webkit-appearance:none;
      -ms-appearance:none;
       /*在选择框的最右侧中间显示小箭头图片*/
       background: url(../../assets/img/appealSelect/select@2x.png) no-repeat scroll right center transparent;
       background-size: .44rem .44rem;
    }

  }
}
</style>



