<template lang="html">
  <div class="form-input-box">
    <div class="form-input-item" :class="{borderB: borderB}">
      <div class="form-input__label" :style="{'width': width}">{{label}}</div>
      <div class="form-input__input">
        <input v-if="inpuType === 'text'" class="input" type="text" name="" @input="$emit('input', curValue)" v-model="curValue"
        :style="{color: color, paddingLeft: paddingLeft}" :disabled="disabled" :placeholder="placeholder">
        <input v-if="inpuType === 'password'" class="input" type="password" name="" @input="$emit('input', curValue)" v-model="curValue"
        :style="{color: color, paddingLeft: paddingLeft}" :disabled="disabled" :placeholder="placeholder">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    inpuType: {
      type: String,
      default: 'text'
    },
    value: {},
    width: {
      type: String,
      default: '1.8rem'
    },
    color: {
      type: String,
      default: '#333'
    },
    paddingLeft: {
      type: String,
      default: '0rem'
    },
    label: {
      type: String,
      required: true
    },
    placeholder: {
      type: String,
      default: '请输入'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    borderB: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    value (val) {
      this.curValue = val
    }
  },
  data () {
    return {
      curValue: this.value
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';

.form-input-box {
  overflow: hidden;
  font-size: .32rem;
  color: @midleColor;
  padding: 0 .22rem;
  background-color: @whiteColor;
  height: .88rem;
  line-height: .88rem;
  .form-input-item {
    height: .88rem;
  }
  .form-input__label {
    float: left;
    text-align: left;
  }
  .form-input__input {
    overflow: hidden;
    position: relative;
    .input {
      font-size: .32rem;
      width: 100%;
      height: .8rem;
      border: none;
      color: @defaultColor;
      &::placeholder {
        color: @lightColor;
      }
      &:disabled {
        background-color: @whiteColor;
      }
    }
  }
}
</style>



