<template lang="html">
  <div class="content-title">
    <p class="content-title__text" :style="{color: color}">{{text}}</p>
  </div>
</template>

<script>
export default {
  props: {
    text: {
      type: String,
      require: true
    },
    color: {
      type: String,
      default: '#999'
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';

.content-title__text {
  font-size: .3rem;
  line-height: .3rem;
  padding: .44rem .22rem .2rem;
}
</style>



