<template lang="html">
  <div class="re-form-box">
    <flm-form-input :label="label" :width="width" :placeholder="placeholder"></flm-form-input>
  </div>
</template>

<script>
export default {
  props: {
    label: {
      type: String,
      default: '输入框:'
    },
    width: {
      type: String,
      default: '1.46rem'
    },
    placeholder: {
      type: String,
      default: '默认文字'
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';

.re-form-box {
  background: @whiteColor;
  border-bottom: 1px solid @lineColor;
  padding: 0 .22rem;
  .form-input__input .input {
    padding-left: .12rem;
  }
}
</style>



